import React from 'react';

import { Container } from './info.style';

const Info = () => {
  return (
    <Container>
      <ul className="gallery">
        <li>
          <div className="content_info">
            <h2>Vlo</h2>
            <p>
              Jai fait un voyage en vlo  14ans.
              <br />
              Core du Sud: Incheon ~ Busan
              <br />
              total: 633km / 6jours
            </p>
          </div>
        </li>
        <li>
          <div className="content_info">
            <h2>Ju-jitsu</h2>
            <p>
              Jai pratiqu de 12  17 ans.
              <br />
              Jai appris plusieurs techniques de dfense.
            </p>
          </div>
        </li>
        <li>
          <div className="content_info">
            <h2>Capoera</h2>
            <p>
              Jai pratiqu de 14  16 ans.
              <br />
              Jai appris des techniques simples grace  la simulation de la comptition en
              utilisant diffrentes actions.
            </p>
          </div>
        </li>
      </ul>
    </Container>
  );
};

export default Info;
